<template>
  <div class="signup">
    <div class="signup-box col-12 col-md-6">
      <div class="title col-12 text-center">
        <h1></h1>
        <b>   .</b>
      </div>

      <div class="form-group">
        <div class="input-box" role="group">
          <label></label>
          <b-input-group>
            <b-form-input
              v-model="id"
              :state="idvalid"
              aria-describedby="id-feedback"
              max="8"
              :disabled="iduq"
              placeholder="  4~8"
              trim
            ></b-form-input>
            <b-input-group-append>
              <b-button
                @click="iduqchk"
                variant="info"
                :disabled="!(idvalid && id != '')"
                ></b-button
              >
            </b-input-group-append>
            <b-form-invalid-feedback id="id-feedback">
               4~8  .
            </b-form-invalid-feedback>
          </b-input-group>
          <b-form-text v-if="iduqtry">{{
            iduq == true
              ? "  ."
              : " ."
          }}</b-form-text>
        </div>

        <div class="input-box" role="group">
          <label></label>
          <b-form-input
            v-model="password"
            :state="pwvalid"
            type="password"
            max="8"
            aria-describedby="pw-feedback"
            placeholder="  4~8"
            trim
          ></b-form-input>

          <b-form-invalid-feedback id="pw-feedback">
             6~8  .
          </b-form-invalid-feedback>
        </div>

        <div class="input-box" role="group">
          <label> </label>
          <b-form-input
            v-model="passwordchk"
            :state="pwchkvalid"
            type="password"
            max="8"
            aria-describedby="pwchk-feedback"
            placeholder="  4~8"
            trim
          ></b-form-input>

          <b-form-invalid-feedback id="pwchk-feedback">
              .
          </b-form-invalid-feedback>
        </div>

        <div class="input-box" role="group">
          <label></label>
          <b-form-input
            v-model="inputname"
            :state="namevalid"
            max="11"
            aria-describedby="name-feedback"
            placeholder=" "
            trim
          ></b-form-input>

          <b-form-invalid-feedback id="name-feedback">
             2~11  .
          </b-form-invalid-feedback>
        </div>

        <div class="input-box" role="group">
          <label></label>
          <b-form-input
            v-model="phone"
            :state="phonevalid"
            max="11"
            aria-describedby="phone-feedback"
            placeholder="'-'   "
            trim
          ></b-form-input>

          <b-form-invalid-feedback id="phone-feedback">
             10~11  .
          </b-form-invalid-feedback>
        </div>

        <div class="input-box" role="group">
          <label></label>
          <b-input-group>
            <b-form-input
              v-model="email"
              :state="emailvalid"
              max="30"
              :disabled="emailuq"
              aria-describedby="email-feedback"
              placeholder=" ."
              trim
            ></b-form-input>
            <b-input-group-append>
              <b-button
                @click="emailuqchk"
                variant="info"
                :disabled="!(emailvalid && email != '')"
                ></b-button
              >
            </b-input-group-append>
            <b-form-invalid-feedback id="email-feedback">
                .
            </b-form-invalid-feedback>
          </b-input-group>
          <b-form-text v-if="emailuqtry">{{
            emailuq == true
              ? "  ."
              : " ."
          }}</b-form-text>
        </div>

        <br />
        <div class="input-box" role="group">
          <button
            class="login-btn btn btn-primary col-12"
            @click="signupOk"
            :disabled="!signbtn"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "signup",
  data() {
    return {
      id: "",
      password: "",
      passwordchk: "",
      email: "",
      phone: "",
      inputname: "",
      iduq: false,
      emailuq: false,
      iduqtry: false,
      emailuqtry: false,
    };
  },
  mounted() {
    if (this.$store.state.isLogin) {
      alert(" !");
      this.$router.push("/");
    }
  },
  computed: {
    idvalid() {
      return (
        (/[a-z0-9]{4,6}/.test(this.id) &&
          /[a-z]/.test(this.id) &&
          /[0-9]/.test(this.id)) ||
        this.id == ""
      );
    },
    pwvalid() {
      return (
        /(?=.*[a-zA-Z])(?=.*\d).{6,8}/.test(this.password) ||
        this.password == ""
      );
    },
    pwchkvalid() {
      return this.password === this.passwordchk || this.passwordchk == "";
    },
    namevalid() {
      return /[-]{2,11}/.test(this.inputname) || this.inputname == "";
    },
    phonevalid() {
      return /[0-9]{10,11}/.test(this.phone) || this.phone == "";
    },
    emailvalid() {
      return (
        /[a-z0-9_+.-]+@([a-z0-9-]+\.)+[a-z0-9]{2,4}/.test(this.email) ||
        this.email == ""
      );
    },

    signbtn() {
      return (
        this.idvalid &&
        this.pwvalid &&
        this.pwchkvalid &&
        this.namevalid &&
        this.phonevalid &&
        this.emailvalid &&
        this.id != "" &&
        this.passwordchk != "" &&
        this.password != "" &&
        this.inputname != "" &&
        this.phone != "" &&
        this.email != "" &&
        this.iduq &&
        this.emailuq
      );
    },
  },
  methods: {
    iduqchk() {
      this.iduqtry = true;
      var con = {
        headers: {
          "X-CSRF-TOKEN": this.$store.state.csrf,
        },
      };

      var form = new URLSearchParams();
      form.append("id", this.id);
      this.$http
        .post(this.$store.state.baseurl + "/pettime/rest/idchk", form, con)
        .then((res) => {
          if (res.data.cnt == 0) {
            this.iduq = true;
          } else {
            this.iduq = false;
          }
          this.$http
            .get(this.$store.state.baseurl + "/pettime/rest/csrf-token")
            .then((res) => {
              this.$store.commit("setCsrf", res.data);
            });
        });
    },

    emailuqchk() {
      this.emailuqtry = true;
      var con = {
        headers: {
          "X-CSRF-TOKEN": this.$store.state.csrf,
        },
      };

      var form = new URLSearchParams();
      form.append("email", this.email);
      this.$http
        .post(this.$store.state.baseurl + "/pettime/rest/emailchk", form, con)
        .then((res) => {
          if (res.data.cnt == 0) {
            this.emailuq = true;
          } else {
            this.emailuq = false;
          }
          this.$http
            .get(this.$store.state.baseurl + "/pettime/rest/csrf-token")
            .then((res) => {
              this.$store.commit("setCsrf", res.data);
            });
        });
    },

    signupOk() {
      var con = {
        headers: {
          "X-CSRF-TOKEN": this.$store.state.csrf,
        },
      };

      var form = new URLSearchParams();
      form.append("id", this.id);
      form.append("password", this.password);
      form.append("name", this.inputname);
      form.append("phone", this.phone);
      form.append("email", this.email);

      this.$http
        .post(this.$store.state.baseurl + "/pettime/rest/signup", form, con)
        .then((res) => {
          if (res.data.cnt == 1) {
            alert("!\n .");
            this.$router.push("login.vue");
          } else {
            alert("  ");
          }
          this.$http
            .get(this.$store.state.baseurl + "/pettime/rest/csrf-token")
            .then((res) => {
              this.$store.commit("setCsrf", res.data);
            });
        });
    },

    // tryLogin() {
    //   alert(" !");
    //   var con = {
    //     headers: {
    //       "X-CSRF-TOKEN": this.$store.state.csrf,
    //     },
    //   };
    //   var form = new URLSearchParams();
    //   form.append("username", this.username);
    //   form.append("password", this.password);
    //   this.$http.post(this.$store.state.baseurl+"/pettime/rest/loginAJAX", form, con).then((res) => {
    //     if (res.data.islogin) {
    //       alert(".");
    //       this.$store.state.isLogin = res.data.islogin;
    //       this.$store.state.userName = res.data.uesrname;
    //       this.$router.push("/");
    //     } else {
    //       alert(res.data.message);
    //     }
    //     this.$http.get(this.$store.state.baseurl+"/pettime/rest/csrf-token").then((res) => {
    //       this.$store.commit("setCsrf", res.data);
    //     });
    //   });
    // },
  },
  created() {},
};
</script>

<style>
.signup .title b {
  font-weight: 300;
  color: rgb(52, 152, 219);
}
.signup .signup-box {
  margin: 5% auto;
}

.signup .input-box {
  margin-top: 1%;
}
</style>
